}

.header {
  border-bottom: 2px solid rgba(7);
  background: linear-gradient(#020024 43%, #090979 77%, #015c6e 100%);
  padding: 20px 40px;
}

.header-nav {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.home-link {
  color: #fff;
  border: 2px solid #fff;
  border-radius: 5px;
  padding: 5px 10px;
  text-decoration: none;
  display: inline-block;
}

.header-text {
  color: #fff;
  justify-content: center;
  margin: 0;
  font-size: 24px;
  display: flex;
}

.footer {
  height: 80px;
  border-bottom: 2px solid rgba(7);
  background: linear-gradient(#015c6e 0%, #090979 40%, #020024 100%);
}

.container {
  background-color: #686868;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 20px;
}

.title {
  margin-left: 30px;
  font-family: Alkatra, cursive;
}

.title-light {
  color: rgba(0, 0, 0, .592);
}

.title-dark {
  color: rgba(255, 255, 255, .5);
}

.weather-state {
  margin-left: auto;
  margin-right: auto;
  text-decoration: underline;
}

.weather-wrap {
  letter-spacing: 1px;
  background-color: rgba(255, 215, 0, .3);
  border: 2px solid rgba(255, 255, 255, .3);
  border-radius: 20px;
  padding: 20px;
  font-family: Roboto Slab, serif;
}

.weather-list {
  background-color: rgba(255, 215, 0, .3);
  border: 2px solid rgba(255, 255, 255, .3);
  border-radius: 20px;
  padding: 15px 20px;
  line-height: 1.5;
  list-style: none;
}

.descr-title {
  margin: 0 auto;
  font-size: 20px;
}

.weather-img {
  width: 100px;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.time-list {
  list-style: none;
}

@media screen and (min-width: 320px) {
  .section {
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    display: flex;
  }

  .map {
    transform: scale(.5);
  }

  .weather-wrap {
    width: 240px;
    flex-direction: column;
    align-content: center;
    align-items: center;
    margin-bottom: 20px;
    display: flex;
  }

  .weather-description {
    width: 100%;
    background-color: rgba(0, 0, 0, 0);
    border: none;
    flex-direction: column;
    padding: 15px 20px 0;
    display: flex;
  }

  .weather-list {
    margin-bottom: 0;
  }
}

@media screen and (min-width: 768px) {
  .container {
    margin-left: auto;
    margin-right: auto;
  }

  .section {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 30px;
    display: flex;
  }

  .map {
    transform: scale(1);
  }

  .weather-wrap {
    width: 500px;
    flex-direction: row;
    gap: 20px;
    display: flex;
  }

  .weather-list {
    margin: 0;
  }

  .weather-description {
    max-width: 220px;
    background-color: rgba(255, 215, 0, .3);
    border: 2px solid rgba(255, 255, 255, .3);
    border-radius: 20px;
    padding-bottom: 15px;
  }

  .wether-data-wrap {
    flex-direction: column;
    gap: 20px;
    display: flex;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    margin-left: auto;
    margin-right: auto;
  }

  .section {
    flex-flow: row;
  }
}

.toggle_switch {
  width: 100px;
  height: 45px;
  position: relative;
}

input[type="checkbox"].switch_3 {
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  width: 100%;
  height: 100%;
  cursor: pointer;
  z-index: 1;
  outline: 0;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

svg.checkbox .outer-ring {
  stroke-dasharray: 375;
  stroke-dashoffset: 375px;
  animation: resetRing .35s ease-in-out forwards;
}

input[type="checkbox"].switch_3:checked + svg.checkbox .outer-ring {
  animation: animateRing .35s ease-in-out forwards;
}

input[type="checkbox"].switch_3:checked + svg.checkbox .is_checked {
  opacity: 1;
  transform: translateX(0)rotate(0deg);
}

input[type="checkbox"].switch_3:checked + svg.checkbox .is_unchecked {
  opacity: 0;
  transform: translateX(-200%)rotate(180deg);
}

svg.checkbox {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

svg.checkbox .is_checked {
  opacity: 0;
  fill: #ff0;
  transform-origin: 50%;
  transition: all 1.5s;
  transform: translateX(200%)rotate(45deg);
}

svg.checkbox .is_unchecked {
  opacity: 1;
  fill: #fff;
  transform-origin: 50%;
  transition: all 1.5s;
  transform: translateX(0)rotate(0deg);
}

@keyframes animateRing {
  to {
    stroke-dashoffset: 0;
    stroke: #b0aa28;
  }
}

@keyframes resetRing {
  to {
    stroke-dashoffset: 0;
    stroke: #233043;
  }
}

